.viewport-container {
  flex: 1;
  position: relative;
  background: 
    linear-gradient(45deg, #f8f9fa 25%, transparent 25%), 
    linear-gradient(-45deg, #f8f9fa 25%, transparent 25%), 
    linear-gradient(45deg, transparent 75%, #f8f9fa 75%), 
    linear-gradient(-45deg, transparent 75%, #f8f9fa 75%);
  background-size: 20px 20px;
  background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
  border: 2px solid #dee2e6;
  border-radius: 8px;
  min-height: 600px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  cursor: crosshair;
}

.main-view {
  width: 100%;
  height: 100%;
  position: relative;
}

.svg-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.svg-layer > div {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.svg-layer svg {
  width: 100% !important;
  height: 100% !important;
  max-width: 100% !important;
  max-height: 100% !important;
  object-fit: contain;
  pointer-events: none;
}

.controls h2 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #333;
  border-bottom: 2px solid #007bff;
  padding-bottom: 10px;
}

.layer-controls {
  margin-bottom: 20px;
}

.layer-control {
  margin: 15px 0;
  padding: 12px;
  background: #f8f9fa;
  border-radius: 6px;
  border-left: 4px solid #007bff;
  transition: all 0.3s ease;
}

.layer-control:hover {
  background: #e9ecef;
  transform: translateX(2px);
}

.layer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.layer-header label {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-weight: 600;
  color: #333;
}

.layer-header input[type="checkbox"] {
  width: 18px;
  height: 18px;
}

.reload-btn {
  width: auto !important;
  padding: 6px 10px !important;
  font-size: 12px !important;
  background: #6c757d !important;
  margin: 0 !important;
  border-radius: 4px !important;
  border: none;
  color: white;
  cursor: pointer;
}

.reload-btn:hover {
  background: #545b62 !important;
}

.layer-path {
  margin-bottom: 10px;
}

.layer-path small {
  color: #666;
  word-break: break-all;
  font-size: 11px;
  background: rgba(0, 0, 0, 0.05);
  padding: 2px 6px;
  border-radius: 3px;
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.error-message {
  background: #f8d7da;
  color: #721c24;
  padding: 8px;
  border-radius: 4px;
  font-size: 12px;
  margin-top: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.error-message button {
  background: #dc3545;
  margin-left: 10px;
  padding: 4px 12px !important;
  font-size: 11px !important;
  width: auto !important;
  border-radius: 3px !important;
  border: none;
  color: white;
  cursor: pointer;
}

.layer-control input[type="range"] {
  width: 100%;
  margin: 10px 0 5px 0;
  height: 6px;
  border-radius: 3px;
  background: #ddd;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

.layer-control input[type="range"]:hover {
  opacity: 1;
}

.opacity-value {
  display: block;
  text-align: center;
  font-size: 12px;
  color: #007bff;
  font-weight: 600;
  margin-top: 4px;
}

button {
  background: #007bff;
  color: white;
  border: none;
  padding: 12px 16px;
  border-radius: 6px;
  cursor: pointer;
  width: 100%;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s ease;
  margin-bottom: 10px;
}

button:hover {
  background: #0056b3;
  transform: translateY(-1px);
}

.reset-btn {
  background: #6f42c1 !important;
}

.reset-btn:hover {
  background: #5a2d91 !important;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 18px;
  color: #666;
}

.svg-error, .svg-loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #666;
  text-align: center;
  padding: 20px;
}

.svg-error button {
  width: auto !important;
  margin-top: 15px !important;
  padding: 10px 20px !important;
  background: #dc3545;
}

.svg-error button:hover {
  background: #c82333;
}

.error-path {
  font-size: 12px;
  color: #999;
  margin: 8px 0;
  background: rgba(0, 0, 0, 0.05);
  padding: 4px 8px;
  border-radius: 3px;
}

.resolution-badge {
  margin-left: 8px;
  font-size: 12px;
}

.resolution-badge.high-res {
  color: #28a745;
}

.resolution-badge.low-res {
  color: #6c757d;
}

.performance-info {
  position: absolute;
  bottom: 10px;
  left: 10px;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 10px;
  border-radius: 6px;
  font-size: 12px;
  z-index: 10;
  border: 1px solid #65d2fe;
  backdrop-filter: blur(10px);
}

.resolution-info, .memory-info, .zoom-info-display {
  margin-bottom: 4px;
}



.resolution-panel {
  background: #e7f3ff;
  padding: 15px;
  border-radius: 6px;
  margin-bottom: 20px;
  border-left: 4px solid #007bff;
}

.resolution-panel h4 {
  margin: 0 0 10px 0;
  color: #0056b3;
}

.resolution-panel ul {
  margin: 0;
  padding-left: 20px;
  font-size: 12px;
}

.manual-controls {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #dee2e6;
}

.manual-controls h4 {
  margin: 0 0 10px 0;
  font-size: 14px;
}

.load-high-res-btn {
  background: #28a745 !important;
  margin-bottom: 5px !important;
}

.unload-high-res-btn {
  background: #dc3545 !important;
  margin-bottom: 0 !important;
}

.zoom-controls {
  position: absolute;
  top: 1rem;
  right: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.95);
  padding: 1rem;
  border-radius: 12px;
  box-shadow: var(--shadow);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-color);
  z-index: 10;
}



.image-container:active {
  cursor: grabbing;
}

.image-transform-container {
  width: 100%;
  height: 100%;
  transition: transform 0.1s ease-out;
}

.image-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.layer-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.image-size {
  display: block;
  margin-top: 2px;
  color: #888;
}

@media (max-width: 768px) {
  .app {
    flex-direction: column;
    height: auto;
  }
  
  .controls {
    width: 100%;
    height: auto;
  }
  
  .viewport-container {
    min-height: 400px;
  }
  
  .magnifier-controls {
    left: 10px;
    right: 10px;
    bottom: 10px;
  }
} 







