  border-radius: 3px;
  background: #e2e8f0;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

.opacity-slider:hover {
  opacity: 1;
}

.opacity-slider::-webkit-slider-thumb {
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--primary-color);
  cursor: pointer;
}

