/* Simple Resolution Modal */
.resolution-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  backdrop-filter: blur(2px);
}

.resolution-modal {
  background: white;
  padding: 1rem 1.5rem;
  border-radius: 8px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  border: 1px solid #e2e8f0;
  animation: modalSlideIn 0.2s ease-out;
  max-width: 280px;
  width: auto;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-10px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-content {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 0.9rem;
  color: #1e293b;
}

.modal-icon {
  color: #2563eb;
  font-size: 1rem;
}

.modal-text {
  font-weight: 500;
}

.action-btn {
  flex: 1;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: all 0.2s ease;
}

.reset-all-btn {
  background: var(--secondary-color);
  color: white;
}

.reset-all-btn:hover {
  background: #475569;
}

.reset-zoom-btn {
  background: var(--primary-color);
  color: white;
}

.reset-zoom-btn:hover {
  background: var(--primary-dark);
}

