}

.footer-stats {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

/* Loading State */
.loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 1.25rem;
  color: var(--text-secondary);
  gap: 1rem;
}

.loading-icon {
  font-size: 2rem;
  color: var(--primary-color);
}

.image-error, .image-loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: var(--text-secondary);
  text-align: center;
  padding: 2rem;
  gap: 1rem;
}

.image-loading svg {
  color: var(--primary-color);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .app-main {
    flex-direction: column;
  }
  
  .controls {
    width: 100%;
    max-height: 400px;
  }
  
  .image-container {
    min-height: 500px;
  }
}

@media (max-width: 768px) {
  .app-header, .app-footer {
    padding: 1rem;
  }
  
  .header-content, .footer-content {
    flex-direction: column;
    gap: 0.5rem;
    text-align: center;
  }
  
  .app-main {
    padding: 1rem;
    gap: 1rem;
  }
  
  .performance-info, .navigation-help {
    position: relative;
    top: auto;
    left: auto;
    margin: 1rem;
  }
  
  .zoom-controls {
    position: relative;
    top: auto;
    right: auto;
    flex-direction: row;
    justify-content: center;
    margin: 1rem;
  }
}